.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  max-width: 480px;
  padding: 48px;
  color: #484848;
  background-color: #ffffff;
  border-radius: 8px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-box-shadow: 0 3px 12px rgba(0, 0, 0, 0.1);
          box-shadow: 0 3px 12px rgba(0, 0, 0, 0.1);
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.modal--opened {
  display: block; }

.modal--closed {
  display: none; }

.modal__close {
  position: absolute;
  top: 31px;
  right: 31px;
  width: 20px;
  height: 19px;
  padding: 0;
  margin: 0;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-size: 18px 17px;
  background-position: center;
  border: 0;
  cursor: pointer; }

.modal h2 {
  margin: 0 0 16px;
  font-size: 28px;
  line-height: 1.2;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #1fc0fc; }

.modal p {
  margin: 0 0 32px;
  font-size: 15px;
  color: rgba(72, 72, 72, 0.87); }

.content h2 {
  margin: 0 0 24px;
  font-weight: 700;
  font-size: 40px;
  line-height: 1.2;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #1fc0fc;
  text-transform: uppercase; }

.content p {
  margin: 0 0 14px; }

.content a {
  color: #1fc0fc; }

.content a:hover {
  text-decoration: none; }

.input {
  position: relative;
  margin-bottom: 28px; }

.input input {
  width: 100%;
  padding: 13px 40px 14px;
  font: inherit;
  font-weight: 700;
  text-transform: uppercase;
  color: rgba(72, 72, 72, 0.87);
  border: 2px solid #e3e3e3;
  border-radius: 80px;
  background-clip: padding-box;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: 0.2s;
  transition: 0.2s; }

.input input::-webkit-input-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input input::-moz-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input input:-ms-input-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input input::-ms-input-placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input input::placeholder {
  color: rgba(72, 72, 72, 0.5); }

.input input:not(:placeholder-shown):not(:focus):valid {
  border-color: rgba(72, 72, 72, 0.5); }

.input input:not(:placeholder-shown):invalid {
  border-color: rgba(255, 0, 0, 0.5); }

.input input:focus,
.input input:not(:invalid):hover,
.input input:placeholder-shown:hover {
  outline: none;
  border-color: transparent; }

.input input:focus ~ label::before,
.input input:hover ~ label::before {
  opacity: 1; }

.input label {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%; }

.input label::before {
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 80px;
  background-image: linear-gradient(108deg, #ffc341 20%, #ffd701 65%);
  opacity: 0;
  -webkit-transition: 0.2s;
  transition: 0.2s; }

.input__error {
  position: absolute;
  top: 100%;
  left: 24px;
  font-size: 13px;
  line-height: 1.5;
  color: #ff0000; }

.header {
